import { style } from 'glamor';
import theme from './_.theme';

export default {
  select: style({
    border: 0,
    margin: '-5px',
    color: theme.base0B,
    width: '250px',
    height: '28px',
    padding: '5px 35px 5px 10px',
    fontSize: '14px',
    appearance: 'none',
    backgroundColor: theme.base00,
    backgroundPosition: 'right 50%',
    backgroundRepeat: 'no-repeat',
    backgroundImage: "url('data:image/png;base64,')",
  }),
};



